#lay_top{height: 50px;line-height: 50px;background-color:#72A69C;font-size: 1.5em;text-align: center;}
#lay_top p{width: 100%;}
#lay_left{background-color:#75c9ba;font-size: 1.5em;text-align: center;}
#lay_left p{width: 100%;}
#lay_contents{font-size: 12px; padding: 20px 10px;}
#lay_contents th{text-align: center;}
#lay_contents td{padding: 10px;}
#lay_bottom{height: 50px;line-height: 50px;background-color:#72A69C;font-size: 1.5em;text-align: center;}
#lay_bottom p{width: 100%;}

input[type=text][name=id]{width: 150px;}
input[type=text][name=title]{width: 100%;}
textarea{width: 100%;height: 100px;}
@font-face {
  font-family: "BM JUA_TTF";
  src: url("./font/BMJUA_TTF.TTF");
}
body{
  font-family: 'BM JUA_TTF';
}